.nav{
    position: fixed;
    top:0;
    width: 100%;
    display: flex;
    padding:20px;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
    
}
.nav_logo{
    width: 80px;
    object-fit: contain;
}
.nav_black{
    background-color: #111;
}